import { Component, OnInit } from '@angular/core'
import { AkunBillingService, AkunBillingCategoryService } from 'src/app/sdk/hr/services'
import { NgxSpinnerService } from 'ngx-spinner'
import { NzNotificationService } from 'ng-zorro-antd'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router, ActivatedRoute } from '@angular/router'
import { Billing, Billing_category } from 'src/app/sdk/hr/models'
import { FormControl, Validators } from '@angular/forms'
import { SingleUploadService } from 'src/app/components/document/single-upload.service'

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    constructor(
        private akunBillingService: AkunBillingService,
        private akunBillingCategoryService: AkunBillingCategoryService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) {}

    toogleUpload = false
    dataBilling: Billing = {}
    listKategori: Billing_category[] = []

    id_billing: string = ''

    ngOnInit() {
        this.id_billing = this.activatedRoute.snapshot.paramMap.get('id_billing')
        this.form()
        this.getlistKategori()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'akutansi_billing.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )

    }

    togleShowUpload() {
        this.toogleUpload = true;
    }

    getlistKategori() {
        this.akunBillingCategoryService.getAkunBillingCategories().subscribe(data => {
            this.listKategori = data.data
        })
    }

    getData() {
        this.spinner.show()
        this.akunBillingService.getAkunBillingsId(this.id_billing).subscribe(data => {
            this.dataForm.setValue(data.data)
            this.spinner.hide()

            this.singleUploadService.param.next({
                for_module: "akutansi_billing",
                for_table: "erp_akutansi.billing",
                for_module_id: data.data.id_billing
            })
        })
    }

    dataForm: FormGroupTypeSafe<Billing>
    form() {
        this.dataForm = this.fb.group<Billing>({
            id_billing_category: new FormControl('', [Validators.required]),
            keterangan: new FormControl(''),
            nilai: new FormControl(''),
            tanggal_cetak: new FormControl(''),
            tanggal_jatuh_tempo: new FormControl(''),
            nama_provider: new FormControl(''),
            no_billing: new FormControl(''),
            id_dokumen: new FormControl(''),
            periode: new FormControl(''),
            id_user: new FormControl(''),
            id_billing: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_data_flow: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            nomor: new FormControl(''),
            status: new FormControl(''),
            posisi: new FormControl(''),
        })
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()


        this.dataBilling = this.dataForm.value
        this.akunBillingService
            .putAkunBillingsId({ id: this.id_billing, body: this.dataBilling })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/ga/account/billing/list'])
    }
}
