import { standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { Billing, Billing_category } from 'src/app/sdk/hr/models';
import { AkunBillingService, AkunBillingCategoryService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core'
import { NgxSpinnerService } from 'ngx-spinner';
import { ActivatedRoute, Router } from '@angular/router';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';
import { StandarApprovalService } from 'src/app/components/approval/standar/standar-approval.service';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss'],
})
export class DetailComponent implements OnInit {
    constructor(
        private akunBillingService: AkunBillingService,
        private akunBillingCategoryService: AkunBillingCategoryService,
        private spinner: NgxSpinnerService,
        private activatedRoute: ActivatedRoute,
        private router: Router,
        private toolsLogFlowService: ToolsLogFlowService,
        private standarApprovalService: StandarApprovalService,
    ) { }

    id_billing: string = ''
    dataBilling: Billing = {}
    dataKategori: Billing_category = {}


    ngOnInit() {
        this.id_billing = this.activatedRoute.snapshot.paramMap.get('id_billing')
        this.getBilling()
        this.getLog()

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getBilling()
                    this.getLog()
                }
            }
        )
    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getBilling() {
        this.spinner.show()
        this.akunBillingService.getAkunBillingsId(this.id_billing).subscribe(
            data => {
                this.dataBilling = data.data;
                this.getKategori(data.data.id_billing_category);

                this.params = {
                    id: data.data.id_billing,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_billing"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )
    }

    getKategori(id_billing_category: string) {
        this.akunBillingCategoryService.getAkunBillingCategoriesId(id_billing_category).subscribe(
            data => {
                this.dataKategori = data.data;
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_billing })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }
    goToBack() {
        this.router.navigate(['/ga/account/billing/list'])
    }
}
